.WatchHeader {
    max-width: 760px;
    background-color: rgba(255,246,229,0.7);
    border-bottom: none;
    border-radius: 5px 5px 0px 0px;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.WatchHeader .thumbnail {
    -ms-flex: 0 0 20%;
        flex: 0 0 20%;
    -ms-flex-item-align: center;
        align-self: center;
    padding: 5px;
    padding-top: 10px;
}

.WatchHeader .title {
    -ms-flex: 0 0 50%;
        flex: 0 0 50%;
    -ms-flex-item-align: center;
        align-self: center;
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.WatchHeader .stock {
    -ms-flex: 0 0 20%;
        flex: 0 0 20%;
    color: green;
    -ms-flex-item-align: center;
        align-self: center;
    font-size: 36px;
    padding-top: 5px;
}

.WatchHeader .stock p {
    margin-top: 5px;
    margin-bottom: 5px;
    font-size: 14px;
    color: black;
}

.WatchHeader img {
    width: 100%;
}


@media (min-width: 640px) {
    .WatchHeader .title {
        font-size: 24px;
    }

    .WatchHeader .stock {
        font-size: 60px;
    }

    .WatchHeader .stock p {
        font-size: 24px;
    }
}


